.footer {
  width: 100%;
  height: 100px;
  background-color: var(--footer-background);
  color: var(--footer);
  padding: 0 80px;
  box-sizing: border-box;

  .footerWrapper {
    width: 100%;
    max-width: 1600px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;

    .footerTitle {
      flex: 5;
      font-family: "Poppins", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 1.4rem;

      .copyright {
        margin-left: 20px;
        font-size: 0.5rem;
      }
    }
    .footerList {
      flex: 1;

      ul {
        flex: 1;
        display: flex;
        justify-content: flex-end;

        /*왼쪽에서 오른쪽으로 이동하는 에니메이션*/
        .to-right-underline {
          position: relative;
        }
        .to-right-underline:after {
          content: "";
          position: absolute;
          left: 0;
          bottom: -5px;
          width: 0px;
          height: 1px;
          margin: 5px 0 0;
          transition: all 0.2s ease-in-out;
          transition-duration: 0.3s;
          opacity: 0;
          background-color: #8c929a;
        }
        .to-right-underline:hover:after {
          width: 100%;
          opacity: 1;
        }

        li {
          margin-left: 50px;
          cursor: pointer;
        }
      }
    }
  }
}

@media all and (max-width: 768px) {
  .footer {
    min-height: 100px;
    padding: 10px 20px 10px 10px;

    .footerWrapper {
      flex-flow: column-reverse nowrap;
      align-items: flex-start;
      justify-content: center;

      .footerTitle {
        display: flex;
        flex-flow: column;

        .copyright {
          margin-left: 0;
          margin-top: 5px;
        }
      }
      .footerList {
        padding: 10px 0;

        ul {
          li {
            margin-left: 0;
            margin-right: 20px;
          }
        }
      }
    }
  }
}
